@font-face {
  font-family: "Helvetica";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/HelveticaRegular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Helvetica";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/HelveticaNeueCyr-Bold.woff2") format("woff2"), url("../fonts/HelveticaNeueCyr-Bold.woff") format("woff");
  font-display: swap;
}
body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  width: auto;
  margin: 0;
  padding: 0;
  font-size: 18px;
  line-height: 18px;
  font-family: "Arial", sans-serif;
  font-weight: 400;
  color: #9F9F9F;
  font-style: normal;
  background-color: #ffffff;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

img {
  max-width: 100%;
  height: auto;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

.wrapper {
  width: 288px;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .wrapper {
    width: 750px;
  }
}
@media (min-width: 1300px) {
  .wrapper {
    width: 1110px;
  }
}
.main-header {
  position: relative;
  margin-top: 20px;
}

.main-header__logo-image {
  position: absolute;
  top: 0;
  right: calc(50% - 39px);
}

@media (min-width: 1300px) {
  .main-header {
    margin-top: 33px;
  }
}
.user-navigation {
  display: flex;
  justify-content: space-between;
}

.user-navigation__list {
  display: flex;
}

.user-navigation__item {
  list-style: none;
}

.user-navigation__link {
  margin-right: 16px;
}

.user-navigation__link--menu-toggle {
  border: 0;
  width: 28px;
  height: 20px;
  cursor: pointer;
  display: block;
  background-image: url("../img/menu.svg");
  background-repeat: no-repeat;
  background-size: 28px 20px;
  background-color: #ffffff;
}

.user-navigation__search-img {
  width: 22px;
  height: 22px;
  cursor: pointer;
  display: block;
  background-image: url("../img/search.svg");
  background-repeat: no-repeat;
  background-size: 22px 22px;
}

.user-navigation__link--user {
  width: 22px;
  height: 25px;
  cursor: pointer;
  display: none;
  background-image: url("../img/user.svg");
  background-repeat: no-repeat;
  background-size: 22px 25px;
}

.user-navigation__link--favorites {
  width: 24px;
  height: 22px;
  cursor: pointer;
  display: block;
  background-image: url("../img/favorites.svg");
  background-repeat: no-repeat;
  background-size: 24px 22px;
}

.user-navigation__link--basket {
  position: relative;
  width: 31px;
  height: 22px;
  cursor: pointer;
  display: block;
  background-image: url("../img/basket.svg");
  background-repeat: no-repeat;
  background-size: 20px 22px;
  padding-right: 11px;
  margin-right: 0;
}

.user-navigation__link--basket-amount {
  position: absolute;
  content: "";
  width: 16px;
  height: 16px;
  right: 0;
  top: -3px;
  background-image: url("../img/amount.svg");
  background-repeat: no-repeat;
  background-size: 16px 16px;
  z-index: 1;
  margin-right: 0;
}

.user-navigation__input-search--opened {
  display: inline-block;
}

.user-navigation__input-search--closed {
  display: none;
}

.user-navigation__link--menu-opened {
  display: none;
}

.user-navigation__link--menu-closed {
  display: block;
}

@media (min-width: 768px) {
  .user-navigation__link--user {
    display: block;
  }
}
@media (min-width: 1300px) {
  .user-navigation__input-search {
    padding-left: 45px;
    border: none;
    width: 295px;
    font-family: "Helvetica", "Arial", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
    color: #000000;
  }
  .user-navigation__input-search:focus-visible {
    outline: none;
  }

  .user-navigation__input-search:hover::placeholder {
    color: #000000;
  }

  .user-navigation__search-wrapper {
    position: relative;
  }

  .user-navigation__search-wrapper::after {
    position: absolute;
    content: "";
    display: block;
    width: 250px;
    height: 1px;
    left: 45px;
    top: 27px;
    background-color: #9F9F9F;
    z-index: 5;
  }

  .user-navigation__search-img {
    position: absolute;
    content: "";
    left: 0;
    z-index: 2;
    background-color: #ffffff;
    width: 25px;
    height: 25px;
    background-size: 25px 25px;
  }

  .main-header__logo-image {
    width: 160px;
    height: 50px;
    top: 69px;
    left: calc(50% - 80px);
  }

  .user-navigation__link {
    margin-right: 22px;
  }

  .user-navigation__link--user {
    display: block;
  }

  .user-navigation__link--favorites {
    width: 28px;
    height: 25px;
    cursor: pointer;
    display: block;
    background-image: url("../img/favorites.svg");
    background-repeat: no-repeat;
    background-size: 28px 25px;
  }

  .user-navigation__link--basket {
    position: relative;
    width: 31px;
    height: 25px;
    cursor: pointer;
    display: block;
    background-image: url("../img/basket.svg");
    background-repeat: no-repeat;
    background-size: 23px 25px;
    padding-right: 11px;
    margin-right: 0;
  }

  .user-navigation__link--basket-amount {
    margin-right: 0;
  }
}
.site-list {
  display: flex;
  margin-top: 71px;
  list-style: none;
  justify-content: space-between;
}

.site-list-wrapper-left,
.site-list-wrapper-right {
  display: flex;
}

.site-list__item {
  margin-right: 30px;
}

.site-list__item a {
  text-decoration: none;
  font-family: "Helvetica", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 21px;
  color: #9F9F9F;
  text-transform: uppercase;
}
.site-list__item a:hover {
  color: #000000;
}

.site-list-wrapper-right .site-list__item:last-child {
  margin-right: 0;
}

.main-nav {
  display: flex;
  justify-content: space-between;
}

.main-nav--closed {
  display: none;
}

.main-nav--opened {
  display: flex;
}

.breadcrumbs {
  list-style: none;
  display: flex;
}

.breadcrumbs__link {
  text-decoration: none;
  font-family: "Helvetica", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 21px;
  color: #9F9F9F;
  margin-right: 10px;
}
.breadcrumbs__link--active {
  color: #000000;
}
.breadcrumbs__link:hover {
  color: #000000;
}

.breadcrumbs__link span {
  margin-left: 10px;
}

.main-container {
  margin-top: 41px;
}

.main-container__wrapper {
  width: 288px;
  margin: 0 auto;
}

@media (min-width: 1300px) {
  .main-container__wrapper {
    width: 1110px;
  }

  .main-container {
    margin-top: 50px;
  }
}
.basket {
  margin-top: 32px;
}

.basket__title {
  font-family: "Helvetica", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 30px;
  line-height: 35px;
  color: #000000;
}

.basket__text {
  display: inline-block;
  margin-right: 5px;
  margin-top: 12px;
  font-family: "Helvetica", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #000000;
}

.basket__link-account {
  display: inline-block;
  text-decoration: none;
  font-family: "Helvetica", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #9F9F9F;
  margin-top: 12px;
}
.basket__link-account:hover {
  color: #000000;
}

.basket__chosen-products {
  margin-top: 40px;
}

.basket__total-wrapper {
  margin-top: 24px;
  display: flex;
  justify-content: space-between;
  font-family: "Helvetica", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 26px;
  line-height: 30px;
  color: #000000;
}

@media (min-width: 1300px) {
  .basket {
    margin-top: 50px;
  }

  .basket__wrapper {
    width: 555px;
    padding-right: 50px;
  }

  .basket {
    position: relative;
  }

  .basket__total-wrapper {
    position: absolute;
    top: 540px;
    right: 0;
    width: 555px;
    height: 136px;
    background-color: rgba(0, 0, 0, 0.8);
    color: #ffffff;
    font-size: 40px;
    line-height: 46px;
    align-items: center;
    padding: 0 30px;
  }

  .basket__title {
    font-size: 40px;
    line-height: 46px;
  }

  .basket__text {
    margin-top: 15px;
  }

  .basket__chosen-products {
    margin-top: 70px;
  }
}
.form {
  margin-top: 32px;
}

.form__list {
  list-style: none;
  display: grid;
  gap: 16px;
}

.form__input-wrapper {
  position: relative;
  height: 46px;
  width: 100%;
}

.form__input {
  width: 100%;
  height: 46px;
  border: none;
  border-bottom: 1px solid #9F9F9F;
  font-family: "Helvetica", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #000000;
  padding-top: 18px;
}
.form__input:focus-visible {
  outline: none;
}
.form__input--error {
  color: #D80909;
  border-bottom: 1px solid #D80909;
}

.form__input--error + .form__label {
  display: none;
}

.form__label {
  position: absolute;
  top: 18px;
  left: 0px;
  font-family: "Helvetica", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #9F9F9F;
  transform-origin: 0 50%;
  transition: transform 200ms, color 200ms;
}

.form__input:focus ~ .form__label, .form__input:not(:placeholder-shown) ~ .form__label {
  transform: translateY(-22px) scale(0.66);
}

.form__message-error {
  position: absolute;
  top: 0;
  left: 0px;
  font-family: "Helvetica", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 14px;
  color: #D80909;
}

#comment {
  padding-top: 18px;
}

.form__iframe-wrapper {
  width: 287px;
  height: 200px;
  overflow: hidden;
  margin-bottom: 8px;
}

.form__iframe-wrapper iframe {
  width: 100%;
}

.form__select {
  position: relative;
  height: 46px;
  border: none;
  border-bottom: 1px solid #9F9F9F;
  font-family: "Helvetica", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #9F9F9F;
  max-width: 100%;
  box-sizing: border-box;
}
.form__select[data-state=active] .form__select-title {
  color: #000000;
}
.form__select[data-state=active] .form__select-title::before {
  transform: translate(-3px, -50%) rotate(-45deg);
}
.form__select[data-state=active] .form__select-title::after {
  transform: translate(3px, -50%) rotate(45deg);
}
.form__select[data-state=active] .form__select-content {
  opacity: 1;
}
.form__select[data-state=active] .form__select-label + .form__select-option + .form__select-label {
  max-height: 40px;
}

.form__select-title {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 18px 0 9px 0;
  cursor: pointer;
}
.form__select-title::before, .form__select-title::after {
  content: "";
  position: absolute;
  bottom: 14px;
  right: 7px;
  display: block;
  width: 9px;
  height: 1px;
  transition: all 0.3s ease-out;
  background-color: #000000;
  transform: translate(-3px, -50%) rotate(45deg);
}
.form__select-title::after {
  transform: translate(3px, -50%) rotate(-45deg);
}

.form__select-label-title {
  position: absolute;
  left: 0px;
  font-family: "Helvetica", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #9F9F9F;
  transform-origin: 0 50%;
  transition: transform 200ms, color 200ms;
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  cursor: pointer;
}

.form__select-content {
  position: absolute;
  top: 46px;
  left: 0px;
  display: flex;
  flex-direction: column;
  width: 100%;
  padding-left: 20px;
  background-color: #ffffff;
  border: 1px solid #9F9F9F;
  border-top: none;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  transition: all 0.3s ease-out;
  opacity: 0;
  z-index: 8;
}

.form__select-option {
  display: none;
}
.form__select-option:checked + label {
  color: #000000;
}
.form__select-option:disabled + label {
  opacity: 0.6;
  pointer-events: none;
}

.form__select-input-title {
  display: none;
}

.form__select-label {
  display: flex;
  align-items: center;
  width: 100%;
  height: 40px;
  max-height: 0;
  padding: 0 19px;
  transition: all 0.2s ease-out;
  cursor: pointer;
  overflow: hidden;
}
.form__select-label + input + .form__select-label {
  border-top: 0 solid #C7CCD160;
}
.form__select-label:hover {
  color: #000000;
}

.form__select-title-wrapper {
  color: #000000;
}

.form__chosen-products-title {
  font-family: "Helvetica", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 26px;
  line-height: 30px;
  color: #000000;
}

.form__chosen-products-list {
  margin-top: 16px;
  display: flex;
  flex-direction: column;
  list-style: none;
}

.form__button {
  margin: 24px 0 40px 0;
}

@media (min-width: 1300px) {
  .form {
    margin-top: 30px;
  }

  .form__list {
    gap: 20px 35px;
    grid-template-columns: 235px 235px;
  }

  .form__input-wrapper--address {
    grid-column: 1/3;
  }

  .form__input-wrapper--email {
    grid-column: 1/3;
  }

  .form__select {
    grid-column: 1/3;
  }

  .form__input-wrapper--comment {
    grid-column: 1/3;
  }

  .form__iframe-wrapper {
    position: absolute;
    width: 555px;
    height: 700px;
    overflow: hidden;
    top: 0;
    right: 0;
  }

  .form__iframe-wrapper iframe {
    width: 555px;
    height: 700px;
  }

  .form__chosen-products-title {
    font-size: 28px;
    line-height: 32px;
  }

  .form__chosen-products-list {
    margin-top: 5px;
  }

  .form__button {
    margin: 0 0 70px 0;
  }
}
.card {
  display: grid;
  grid-template-columns: 80px 92px 92px;
  gap: 12px;
  padding-bottom: 14px;
  border-bottom: 1px solid #9F9F9F;
  margin-top: 16px;
}

.card__image-wrapper {
  grid-row: 1/4;
}

.card__image {
  display: block;
}

.card__name {
  padding-right: 27px;
  font-family: "Helvetica", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  color: #000000;
  white-space: nowrap;
  grid-column: 2/3;
}

.card__amount-wrapper {
  padding: 0 8px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 81px;
  height: 34px;
  border: 1px solid #000000;
  align-self: end;
  justify-self: start;
  grid-column: 2/3;
  grid-row: 3/4;
  margin-top: 3px;
}

.card__price {
  padding-left: 7px;
  font-family: "Helvetica", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  color: #000000;
  white-space: nowrap;
  justify-self: end;
  grid-row: 1/3;
  grid-column: 3/4;
}

.card__description {
  display: none;
  grid-column: 2/3;
  grid-row: 2/3;
}

.card__button-delete {
  border: none;
  background-color: transparent;
  font-family: "Helvetica", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 14px;
  color: #9F9F9F;
  width: 48px;
  height: 14px;
  align-self: end;
  justify-self: end;
  display: block;
  cursor: pointer;
  grid-column: 3/4;
  border-bottom: 1px solid #9F9F9F;
  padding-bottom: 2px;
}
.card__button-delete:hover {
  color: #000000;
  border-bottom: 1px solid #000000;
}

.card__amount {
  font-family: "Helvetica", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  color: #000000;
}

.card__button-minus {
  width: 12px;
  height: 34px;
  border: none;
  background-color: transparent;
  background-image: url("../img/minus.svg");
  background-repeat: no-repeat;
  background-size: 12px 2px;
  background-position: center;
  cursor: pointer;
}

.card__button-plus {
  width: 12px;
  height: 34px;
  border: none;
  background-color: transparent;
  background-image: url("../img/plus.svg");
  background-repeat: no-repeat;
  background-size: 12px 12px;
  background-position: center;
  cursor: pointer;
}

@media (min-width: 1300px) {
  .card {
    grid-template-columns: 150px 186px 95px;
    gap: 10px 20px;
    margin-top: 25px;
    padding-bottom: 25px;
  }
  .card:last-child {
    border-bottom: none;
  }

  .card__image {
    width: 150px;
  }

  .card__description {
    display: block;
    font-family: "Helvetica", "Arial", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    color: #9F9F9F;
    width: 150px;
  }

  .card__name {
    font-size: 20px;
    line-height: 23px;
  }

  .card__price {
    font-size: 20px;
    line-height: 23px;
  }

  .card__amount {
    font-size: 20px;
    line-height: 23px;
  }

  .card__button-minus {
    width: 16px;
    height: 40px;
    background-size: 16px 2px;
  }

  .card__button-plus {
    width: 16px;
    height: 40px;
    background-size: 16px 16px;
  }

  .card__amount-wrapper {
    height: 40px;
    width: 114px;
    padding: 0 12px 0 15px;
    margin-top: 10px;
  }

  .card__button-delete {
    font-size: 14px;
    line-height: 16px;
    width: 56px;
    height: 16px;
  }
}
.button {
  width: 100%;
  background-color: transparent;
  border: 1px solid #000000;
  padding: 18.5px 0;
  align-items: center;
  font-family: "Helvetica", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 23px;
  color: #000000;
  cursor: pointer;
}
.button:hover {
  background-color: #000000;
  color: #ffffff;
}

@media (min-width: 1300px) {
  .button {
    padding: 24px 0 23px 0;
  }
}
#map {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}